.formMain
{
    body {
        margin: 0;
        padding: 0;
        font-family: Arial, sans-serif;
    }

    .container {
        width: 80%;
        max-width: 600px;
        height: 80%;
        max-height:550px;
        margin: 50px auto;
        padding: 20px;
        border: 1px solid #ccc;
        border-radius: 8px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between; 
        overflow: hidden;
    }

    h1 {
        text-align: center;
        color: #333;
    }

    p {
        text-align: center;
        color: #666;
    }

.container {
position: relative;
}

.goBackButton {
position: absolute;
top: 20px;
left: 20px;
background-color:green ;
color: #fff;
padding: 8px 12px;
border: none;
border-radius: 4px;
cursor: pointer;
font-size: 14px;
box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
z-index: 1000;
}   

.goBackButton:hover {
background-color: #1a1a1a;
}
}