.dashboardMain
{
    *{
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
    
    
}

.numberOfVisitorsHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0; 
   

    p{
        font-size: 16px; /* Remove default margin */
        font-family: 'Poppins', sans-serif;
    }
  }
  
  .numberOfVisitorsHeader img {
    width: 75px; /* Adjust the icon width as needed */
    height: 75px; /* Adjust the icon height as needed */
  }
  

html, body {
  height: 30%;
  margin: 0;
}

body {
  scroll-behavior: smooth;
}
a {
    text-decoration: none;
  }

.SideNav{
    width: 100px;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    transition: width 0.3s;
    background-color: #2C2C2C;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    &:hover{
        width: 300px;

        .CompanyLogo{
            width: 100%;
        }

        ul li p{
            display: block;
        }

        ul li:hover{
            transition: 0.125s ease-in-out;
            padding-left: 15px;
            background-color: #808080;
            
        } 
    }

    .CompanyLogo{
        display: flex;
        align-items: center;
        width: 100%;
        font-size: 20px;
        padding: 10px 10px;
        padding: 10px 10px;
        border-bottom: 1px solid white;
        overflow: hidden;

            .icon{
                width: 70px;
                height: 70px;
            }
    }
    }
    .CompanyLogo h3{
        color: white;
        margin-left: 30px;
    }

    ul{
        list-style: none;

        li{
            display: flex;
            align-items: center;
            justify-content: flex-start;
            margin: 40px 0;
            cursor: pointer;
            

            img{
                width: 50px;
                height: 50px;
                margin-left: 15px;
                margin-right: 20px;
            }


            p{
                color: #9E9E9E;
                font-weight: 600;
                font-size: 20px;
                white-space: nowrap;
                display: none;
            }
        }
    }


    .LogOut{
        margin-top: auto;
        margin-top: auto;
    }
}

.DashboardContent.sideNavHovered {
    left: 300px;
    width: calc(100% - 300px);

    .TodayVisits {
        margin-left: 225px;
        margin-right: 75px;
    }

    .Graphs {
        margin-left: 215px;
    }

    .EmployeeList {
        margin-right: 175px;
    }
    justify-content: flex-start;

}

.DashboardContent{
    position: relative;
    min-height: 100vh;
    min-height: 100vh;
    top: 0;
    left: 75px; 
    transition: left 0.3s, width 0.3s;
    background-color: white;

    h1{
        margin-top: 20px;
        color: black;
        margin-left: 80px;
        font-weight: 900;
        font-size: 50px;
    }
    
    .dashboard-container {
        width: 125vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;

        .small-container {
            margin-top: 30px;
            margin-left: -32px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
        }
        .TodayVisits{
            width: 25vw;
            height: 28vh;
            gap: 10px;
            overflow: hidden;
            background-color: white;
            border: 1px solid black;
            border-radius: 10px;
            box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.48); 
            display: flex;
            flex-direction: column;
            

            .small-container {
                margin-top: 20px;
                display: flex;
                flex-direction: row;
                font-size: 50px;
                font-weight: 700;
                justify-content: space-between;

                img{
                    width: 4vw;
                    height: 7vh;
                }
            }
        }
        
        .TodayVisits{
            margin-left: 125px;
        }
        
        .TotalVisits{
            margin-right: 125px;
        }
    }
    
    .DashboardInsights{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        gap: 20px;

        .Graphs{
            display: flex;
            flex-direction: column;
            gap: 40px;
            align-items: center;
            margin-top: 50px;
            margin-left: 115px;
            
            .TodayVisits{
                margin-left: 125px;
            }
            
            .TotalVisits{
                margin-right: 125px;
            }
        }

        .WeeklyVisitsGraph, .MonthlyVisitsGraph{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: flex-start;
            width: 53vw;
            max-width: 46.5vw;
            height: 58vh;
            padding: 20px;
            margin-left: -30px;
            gap: 50px;
            overflow: hidden;
            background-color: white;
            border: 1px solid black;
            border-radius: 10px;
            box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.48); 
        }
        
        .EmployeeList{
            width: 60vw;
            height: 65vh;
            max-height: 65vh;
            padding: 20px;
            margin: 20px;
            display: flex;
            overflow: hidden;
            flex-direction: column;
            gap: 10px;
            background-color: white;
            border: 1px solid black;
            border-radius: 10px;
            box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.48); 
            margin-right: auto;
            .h2{
                font-size: 45px;
                font-weight: 600;
            }
        }
    }
    
}